/* BHABIT Design System - Enhanced Visual Hierarchy & Effects */

/* CSS Custom Properties for consistent theming */
:root {
  --primary-orange: #FF6B00;
  --primary-blue: #00BFFF;
  --primary-pink: #FF69B4;
  --accent-purple: #8A2BE2;
  --dark-bg: #000;
  --mid-bg: #111;
  --light-bg: #1a1a1a;
  --glass-bg: rgba(0, 0, 0, 0.8);
  --glass-border: rgba(0, 191, 255, 0.3);
  --text-light: #ccc;
  --text-white: #fff;
  --text-muted: #888;
  --shadow-glow: 0 0 30px;
  --shadow-intense: 0 0 50px;
  --transition-smooth: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-quick: all 0.3s ease;
  --transition-bounce: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --border-radius: 20px;
  --border-radius-small: 10px;
  --section-spacing: 6rem; /* Reduced from 12rem */
}

/* Base styles with improved typography */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Unbounded', 'Inter', -apple-system, sans-serif;
  background: var(--dark-bg);
  color: var(--text-white);
  line-height: 1.6;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

/* Enhanced typography hierarchy */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 1.5rem 0;
}

h1 {
  font-size: clamp(2.5rem, 5vw, 4rem); /* Reduced from 6vw, 5rem */
  font-weight: 900;
  color: var(--text-white);
  text-transform: uppercase;
  letter-spacing: -1px;
}

h2 {
  font-size: clamp(1.8rem, 3.5vw, 2.5rem); /* Reduced from 5vw, 4rem */
  color: var(--primary-blue);
  position: relative;
  margin-bottom: 1.5rem;
  text-align: center;
}

h2::before {
  content: "";
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-orange), var(--primary-pink));
  border-radius: 2px;
  box-shadow: 0 0 20px rgba(255, 105, 180, 0.5);
}

h2::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 120px;
  height: 2px;
  background: linear-gradient(90deg, var(--primary-pink), var(--primary-blue));
  border-radius: 2px;
}

h3 {
  color: var(--primary-blue);
  font-size: clamp(1.3rem, 2.5vw, 1.6rem); /* Reduced from 3.5vw, 2.2rem */
  font-weight: 600;
  margin-bottom: 1rem;
  position: relative;
}

h3::before {
  content: "‚ñ∂";
  color: var(--primary-pink);
  margin-right: 0.5rem;
  font-size: 0.8em;
  animation: pulse-arrow 2s infinite;
}

h4 {
  color: var(--primary-pink);
  font-size: clamp(1.3rem, 2.8vw, 1.8rem);
  font-weight: 600;
  margin: 2rem 0 1rem 0;
}

p {
  font-size: clamp(0.95rem, 1.8vw, 1.1rem); /* Reduced from 2.2vw, 1.3rem */
  margin-bottom: 1.5rem;
  color: var(--text-light);
  line-height: 1.6;
}

.lead-text {
  font-size: clamp(1.1rem, 2vw, 1.3rem); /* Reduced from 2.5vw, 1.6rem */
  color: var(--text-white);
  font-weight: 500;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Enhanced navigation with better hierarchy */
.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 2rem;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(25px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: var(--transition-quick);
}

.nav-container.scrolled {
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(30px);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid rgba(0, 191, 255, 0.3);
}

.nav-link {
  color: var(--primary-blue);
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  padding: 0.8rem 1.5rem;
  margin: 0 0.3rem;
  border-radius: var(--border-radius-small);
  transition: var(--transition-bounce);
  position: relative;
  overflow: hidden;
  border: 2px solid transparent;
}

.nav-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 191, 255, 0.2), transparent);
  transition: var(--transition-quick);
}

.nav-link:hover::before {
  left: 100%;
}

.nav-link:hover {
  color: var(--text-white);
  text-shadow: var(--shadow-glow) var(--primary-blue);
  transform: translateY(-3px) scale(1.05);
  border-color: var(--primary-blue);
  background: rgba(0, 191, 255, 0.1);
}

.nav-link.active {
  color: var(--primary-pink);
  background: rgba(255, 105, 180, 0.15);
  text-shadow: var(--shadow-glow) var(--primary-pink);
  border-color: var(--primary-pink);
  transform: scale(1.1);
}

/* Enhanced CTA buttons */
.cta-button, .cta-primary, .cta-secondary {
  display: inline-block;
  padding: 1.2rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: var(--border-radius);
  transition: var(--transition-bounce);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  letter-spacing: 1px;
  margin: 0.5rem;
}

.cta-primary {
  background: linear-gradient(135deg, var(--primary-orange), var(--primary-pink));
  color: var(--text-white);
  box-shadow: 
    0 8px 25px rgba(255, 107, 0, 0.4),
    0 0 40px rgba(255, 105, 180, 0.3);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.cta-primary:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 
    0 15px 35px rgba(255, 107, 0, 0.6),
    0 0 60px rgba(255, 105, 180, 0.5);
  filter: brightness(1.1);
}

.cta-secondary {
  background: transparent;
  color: var(--primary-blue);
  border: 2px solid var(--primary-blue);
  box-shadow: 0 0 20px rgba(0, 191, 255, 0.3);
}

.cta-secondary:hover {
  background: var(--primary-blue);
  color: var(--dark-bg);
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 10px 30px rgba(0, 191, 255, 0.5);
}

.cta-group {
  text-align: center;
  margin: 3rem 0;
}

/* Enhanced layout system with better separation */
.content-container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

section {
  padding: var(--section-spacing) 0;
  position: relative;
  scroll-margin-top: 120px;
}

/* Section separators with visual effects */
section:not(.hero-section):not(:last-child)::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 10%;
  right: 10%;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent, 
    var(--primary-blue), 
    var(--primary-pink), 
    var(--primary-blue),
    transparent);
  box-shadow: 0 0 20px rgba(0, 191, 255, 0.5);
}

section:nth-child(even) {
  background: linear-gradient(135deg, 
    rgba(17, 17, 17, 0.3) 0%, 
    rgba(26, 26, 26, 0.5) 100%);
}

section:nth-child(odd):not(.hero-section) {
  background: linear-gradient(135deg, 
    rgba(0, 0, 0, 0.8) 0%, 
    rgba(17, 17, 17, 0.9) 100%);
}

/* Hero Section - Premium BHABIT logo with enhanced effects */
.hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: 
    radial-gradient(circle at 20% 80%, rgba(0, 191, 255, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 105, 180, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(255, 107, 0, 0.05) 0%, transparent 60%),
    linear-gradient(135deg, var(--dark-bg) 0%, var(--mid-bg) 100%);
  position: relative;
  padding-top: 120px;
  overflow: hidden;
}

.hero-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="20" height="20" patternUnits="userSpaceOnUse"><path d="M 20 0 L 0 0 0 20" fill="none" stroke="rgba(0,191,255,0.08)" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/>'),
    radial-gradient(circle at 30% 70%, rgba(255, 105, 180, 0.1) 0%, transparent 30%);
  z-index: -1;
  animation: grid-pulse 8s ease-in-out infinite alternate;
}

.hero-content {
  position: relative;
  z-index: 2;
}

.hero-section h1 {
  font-size: clamp(3rem, 8vw, 6rem); /* Reduced from 12vw, 9rem */
  font-weight: 900;
  margin-bottom: 1.5rem;
  color: var(--primary-orange);
  text-shadow: 
    var(--shadow-glow) rgba(255, 107, 0, 0.6),
    var(--shadow-intense) rgba(255, 107, 0, 0.3);
  transition: var(--transition-smooth);
  position: relative;
  cursor: pointer;
  letter-spacing: -3px;
  font-family: 'Unbounded', sans-serif;
  text-transform: uppercase;
  filter: drop-shadow(0 5px 15px rgba(255, 107, 0, 0.4));
}

/* Enhanced hover effects with more drama */
.hero-section h1:hover {
  color: var(--primary-blue);
  text-shadow: 
    var(--shadow-intense) rgba(0, 191, 255, 1),
    0 0 80px rgba(255, 105, 180, 0.6),
    0 0 120px rgba(0, 191, 255, 0.4);
  transform: scale(1.03) translateY(-8px) rotateX(5deg);
  filter: drop-shadow(0 15px 30px rgba(0, 191, 255, 0.5));
}

/* Sophisticated backdrop effect with more layers */
.hero-section h1::before {
  content: "";
  position: absolute;
  top: -60px;
  left: -100px;
  right: -100px;
  bottom: -60px;
  background: 
    radial-gradient(ellipse at center, 
      rgba(255, 105, 180, 0) 0%, 
      rgba(255, 105, 180, 0.15) 25%,
      rgba(255, 105, 180, 0.25) 40%, 
      rgba(0, 191, 255, 0.15) 70%,
      rgba(138, 43, 226, 0.1) 85%,
      transparent 100%);
  border-radius: 50%;
  opacity: 0;
  transition: var(--transition-smooth);
  z-index: -1;
  filter: blur(25px);
}

.hero-section h1:hover::before {
  opacity: 1;
  transform: scale(1.4) rotate(10deg);
  animation: backdrop-pulse 3s ease-in-out infinite alternate;
}

/* Enhanced drip effects with more complexity */
.hero-section h1::after {
  content: "";
  position: absolute;
  bottom: -80px;
  left: 10%;
  width: 80%;
  height: 60px;
  background: linear-gradient(to bottom, 
    rgba(255, 107, 0, 0.8) 0%, 
    rgba(255, 107, 0, 0.4) 20%, 
    rgba(255, 107, 0, 0.2) 50%,
    rgba(255, 107, 0, 0.1) 80%,
    transparent 100%);
  border-radius: 80px 80px 40px 40px;
  filter: blur(4px);
  z-index: -1;
  transition: var(--transition-smooth);
  animation: enhanced-drip 5s ease-in-out infinite alternate;
  box-shadow: 0 0 30px rgba(255, 107, 0, 0.4);
}

.hero-section h1:hover::after {
  background: linear-gradient(to bottom, 
    rgba(0, 191, 255, 0.9) 0%, 
    rgba(255, 105, 180, 0.7) 30%, 
    rgba(0, 191, 255, 0.4) 60%,
    rgba(255, 105, 180, 0.2) 80%,
    transparent 100%);
  width: 90%;
  left: 5%;
  height: 80px;
  box-shadow: 
    0 0 40px rgba(0, 191, 255, 0.6),
    0 0 60px rgba(255, 105, 180, 0.4);
}

/* Premium splash effects with more variety */
.hero-section h1 .splash-effect {
  position: absolute;
  border-radius: 50%;
  filter: blur(5px);
  z-index: -1;
  transition: var(--transition-smooth);
  opacity: 0.8;
}

.hero-section h1 .splash-1 {
  top: -50px;
  left: -100px;
  width: 70px;
  height: 70px;
  background: radial-gradient(circle, 
    rgba(255, 107, 0, 0.6) 0%, 
    rgba(255, 107, 0, 0.3) 40%,
    rgba(255, 107, 0, 0.1) 70%,
    transparent 80%);
  animation: splash-float-1 6s ease-in-out infinite alternate;
  box-shadow: 0 0 20px rgba(255, 107, 0, 0.3);
}

.hero-section h1 .splash-2 {
  top: 30px;
  right: -110px;
  width: 60px;
  height: 60px;
  background: radial-gradient(circle, 
    rgba(255, 107, 0, 0.5) 0%, 
    rgba(255, 107, 0, 0.2) 50%,
    rgba(255, 107, 0, 0.1) 70%,
    transparent 80%);
  animation: splash-float-2 5s ease-in-out infinite alternate-reverse;
  box-shadow: 0 0 15px rgba(255, 107, 0, 0.25);
}

.hero-section h1 .splash-3 {
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 45px;
  background: radial-gradient(ellipse, 
    rgba(255, 107, 0, 0.6) 0%, 
    rgba(255, 107, 0, 0.3) 50%,
    rgba(255, 107, 0, 0.1) 80%,
    transparent 90%);
  animation: splash-float-3 6.5s ease-in-out infinite alternate;
  box-shadow: 0 0 25px rgba(255, 107, 0, 0.35);
}

/* Enhanced hover states with more dramatic effects */
.hero-section h1:hover .splash-1 {
  background: radial-gradient(circle, 
    rgba(0, 191, 255, 0.8) 0%, 
    rgba(255, 105, 180, 0.6) 30%,
    rgba(0, 191, 255, 0.4) 60%,
    rgba(138, 43, 226, 0.2) 80%,
    transparent 90%);
  transform: scale(1.6) rotate(35deg);
  box-shadow: 
    0 0 30px rgba(0, 191, 255, 0.5),
    0 0 50px rgba(255, 105, 180, 0.3);
}

.hero-section h1:hover .splash-2 {
  background: radial-gradient(circle, 
    rgba(255, 105, 180, 0.8) 0%, 
    rgba(0, 191, 255, 0.5) 40%,
    rgba(255, 105, 180, 0.3) 70%,
    transparent 85%);
  transform: scale(1.5) rotate(-25deg);
  box-shadow: 
    0 0 25px rgba(255, 105, 180, 0.5),
    0 0 40px rgba(0, 191, 255, 0.3);
}

.hero-section h1:hover .splash-3 {
  background: radial-gradient(ellipse, 
    rgba(0, 191, 255, 0.7) 0%, 
    rgba(255, 105, 180, 0.5) 30%,
    rgba(0, 191, 255, 0.3) 60%,
    rgba(138, 43, 226, 0.2) 80%,
    transparent 90%);
  transform: translateX(-50%) scale(1.8);
  box-shadow: 
    0 0 35px rgba(0, 191, 255, 0.5),
    0 0 55px rgba(255, 105, 180, 0.4);
}

/* Refined hero subtitle with better hierarchy */
.hero-section .subtitle {
  color: var(--primary-blue);
  font-size: clamp(1.2rem, 2.5vw, 1.8rem); /* Reduced from 4vw, 2.8rem */
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-shadow: 
    0 0 25px rgba(255, 105, 180, 0.6),
    0 0 40px rgba(0, 191, 255, 0.3);
  opacity: 0.95;
  letter-spacing: 1px;
}

.hero-section p {
  color: var(--text-light);
  font-size: clamp(1.2rem, 2.8vw, 1.6rem);
  font-weight: 400;
  max-width: 700px;
  margin: 0 auto 3rem auto;
  opacity: 0.9;
}

/* Enhanced content sections with dramatic separation */
.content-section {
  padding: var(--section-spacing) 0;
  position: relative;
  border-radius: 0;
  margin: 2rem 0;
}

.content-section::before {
  content: "";
  position: absolute;
  top: -2rem;
  left: 0;
  right: 0;
  height: 4rem;
  background: linear-gradient(180deg,
    transparent 0%,
    rgba(0, 191, 255, 0.03) 50%,
    transparent 100%);
  pointer-events: none;
  z-index: -1;
}

.section-container {
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border-radius: var(--border-radius);
  padding: 3rem; /* Reduced from 5rem */
  max-width: 1200px;
  margin: 0 auto;
  border: 1px solid var(--glass-border);
  box-shadow: 
    0 15px 50px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.1),
    0 0 80px rgba(0, 191, 255, 0.1);
  position: relative;
  overflow: hidden;
  transition: var(--transition-smooth);
}

.section-container:hover {
  transform: translateY(-5px);
  box-shadow: 
    0 25px 70px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.15),
    0 0 100px rgba(0, 191, 255, 0.15);
}

.section-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent, 
    var(--primary-orange), 
    var(--primary-blue), 
    var(--primary-pink), 
    transparent);
  opacity: 0.8;
}

.section-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at var(--mouse-x, 50%) var(--mouse-y, 50%), 
    rgba(0, 191, 255, 0.05) 0%, 
    transparent 40%);
  pointer-events: none;
  opacity: 0;
  transition: var(--transition-quick);
}

.section-container:hover::after {
  opacity: 1;
}

/* Enhanced whitepaper section with better visual hierarchy */
.whitepaper-section {
  background: 
    linear-gradient(180deg, 
      var(--mid-bg) 0%, 
      rgba(17, 17, 17, 0.98) 30%,
      rgba(26, 26, 26, 0.95) 70%,
      var(--dark-bg) 100%),
    radial-gradient(circle at 70% 30%, rgba(255, 105, 180, 0.08) 0%, transparent 50%);
  padding: var(--section-spacing) 0;
  position: relative;
}

.whitepaper-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 200"><defs><pattern id="dots" width="20" height="20" patternUnits="userSpaceOnUse"><circle cx="10" cy="10" r="1" fill="rgba(0,191,255,0.1)"/></pattern></defs><rect width="200" height="200" fill="url(%23dots)"/></svg>');
  z-index: -1;
  opacity: 0.5;
}

.whitepaper-toc {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border-radius: var(--border-radius);
  padding: 4rem;
  margin-bottom: 5rem;
  border: 2px solid var(--glass-border);
  box-shadow: 
    0 15px 40px rgba(0, 0, 0, 0.3),
    0 0 60px rgba(0, 191, 255, 0.1);
  position: relative;
}

.whitepaper-toc::before {
  content: "üìã";
  position: absolute;
  top: -15px;
  left: 30px;
  font-size: 2rem;
  background: var(--dark-bg);
  padding: 0 1rem;
}

.whitepaper-toc h3 {
  color: var(--primary-blue);
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
  font-size: clamp(1.8rem, 3.5vw, 2.5rem);
}

.whitepaper-toc-list {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 1rem;
}

.whitepaper-toc-list li {
  position: relative;
}

.whitepaper-toc-list li::before {
  content: "‚ñ∏";
  color: var(--primary-pink);
  margin-right: 0.8rem;
  font-weight: bold;
  transition: var(--transition-quick);
}

.whitepaper-toc-list li:hover::before {
  color: var(--primary-orange);
  transform: scale(1.2);
}

.whitepaper-toc-list li a {
  color: var(--primary-blue);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  transition: var(--transition-bounce);
  padding: 1rem 1.5rem;
  display: block;
  border-radius: var(--border-radius-small);
  border-left: 4px solid transparent;
  position: relative;
  overflow: hidden;
}

.whitepaper-toc-list li a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(90deg, 
    rgba(0, 191, 255, 0.1), 
    rgba(255, 105, 180, 0.1));
  transition: var(--transition-quick);
}

.whitepaper-toc-list li a:hover::before {
  width: 100%;
}

.whitepaper-toc-list li a:hover {
  color: var(--text-white);
  border-left-color: var(--primary-pink);
  text-shadow: 0 0 15px var(--primary-blue);
  transform: translateX(12px) scale(1.02);
  background: rgba(0, 191, 255, 0.05);
}

/* Enhanced whitepaper content with better readability */
.wp-content {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(15px);
  border-radius: var(--border-radius);
  padding: 5rem;
  border: 1px solid rgba(0, 191, 255, 0.3);
  position: relative;
  box-shadow: 
    0 20px 60px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.wp-content h3 {
  color: var(--primary-blue);
  font-size: clamp(1.8rem, 3.5vw, 2.5rem);
  margin: 4rem 0 2.5rem 0;
  padding-bottom: 1.5rem;
  border-bottom: 3px solid;
  border-image: linear-gradient(90deg, var(--primary-pink), var(--primary-blue)) 1;
  position: relative;
}

.wp-content h3:first-child {
  margin-top: 0;
}

.wp-content h3::before {
  content: "‚óè";
  color: var(--primary-orange);
  margin-right: 1rem;
  font-size: 0.8em;
  animation: pulse-dot 2s infinite;
}

